import Navbar from "../../components/Navbar/Navbar";
import Promotion from "../../components/Announcement/Announcement";
import PopularProducts from "../../components/PopularProducts/PopularProducts";
import Product from "../../components/Product/Product";
import Newsletter from "../../components/Newsletter/Newsletter";
import Footer from "../../components/Footer/Footer";
import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import { publicRequest } from "../../requestMethods";
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";

import {
  Action,
  Container,
  Left,
  Text,
  TextContainer,
  Title,
  WishContainer,
  WishWrapper,
  Wrapper,
} from "./styles";

const Wishlist = () => {
  const history = useHistory();
  const wishlist = useSelector((state) => state.wish.wishlist);
  const [products, setProducts] = useState([]);
  useEffect(() => {
    const getProducts = async () => {
      try {
        const response = await publicRequest.get(`products`);
        setProducts(
          response.data.filter((item) => wishlist.includes(item._id))
        );
      } catch (error) {
        console.error(error);
      }
    };
    getProducts();
  }, [wishlist]);
  return (
    <Container>
      <Promotion />
      <Navbar />
      <Wrapper>
        <Title>Seus favoritos</Title>
        <TextContainer>
          <Left>
            <Action
              onClick={() => {
                history.goBack();
              }}
            >
              <ArrowRightAltIcon style={{ transform: "rotate(180deg)" }} />
              <Text>Voltar</Text>
            </Action>
          </Left>
        </TextContainer>
        <WishContainer>
          <WishWrapper>
            {products?.length === 0 && (
              <TextContainer style={{ margin: "50px auto" }}>
                <Text>Você não possui favoritos</Text>
              </TextContainer>
            )}
            {products?.map((item) => (
              <Product item={item} key={item._id} />
            ))}
          </WishWrapper>
        </WishContainer>
      </Wrapper>
      <PopularProducts description="CONFIRA TAMBÉM" />
      <Newsletter />
      <Footer />
    </Container>
  );
};

export default Wishlist;
